import Link from "next/link";
import Header from "../components/Header";

class Index extends React.Component {
  render() {
    return (
      <div>
        <Header />
        <p>Hello World!</p>
        <Link href="/contact">
          <a>Contact me!</a>
        </Link>
        <style jsx>{`
          h1,
          a {
            font-family: "Arial";
          }

          ul {
            padding: 0;
          }

          li {
            list-style: none;
            margin: 5px 0;
          }

          a {
            text-decoration: none;
            color: blue;
          }

          a:hover {
            opacity: 0.6;
          }
        `}</style>
      </div>
    );
  }
}
export default Index;



